import { Film, MessageCircle, Plus, LogOut } from "lucide-react";
import { Button } from "./ui/button";

interface NavigationProps {
  currentView: "timeline" | "messages" | "new-memory";
  onViewChange: (view: "timeline" | "messages" | "new-memory") => void;
  onSignOut: () => void;
  unreadCount?: number;
}

export function Navigation({
  currentView,
  onViewChange,
  onSignOut,
  unreadCount = 0,
}: NavigationProps) {
  return (
    <nav className="bg-gradient-to-b from-card to-card/80 border-b-2 border-primary/40 sticky top-0 z-50 shadow-2xl shadow-primary/10 backdrop-blur">
      <div className="max-w-7xl mx-auto px-4 py-4">
        <div className="flex items-center justify-between">
          <button
            onClick={() => onViewChange("timeline")}
            className="flex items-center gap-3 group"
          >
            <div className="w-12 h-12 bg-gradient-to-br from-primary to-destructive border-2 border-accent/60 flex items-center justify-center group-hover:from-primary/80 group-hover:to-destructive/80 transition-all shadow-lg shadow-primary/20">
              <Film className="w-6 h-6 text-accent" />
            </div>
            <div className="hidden sm:block text-left">
              <div className="text-sm uppercase tracking-widest text-accent">
                Cinema
              </div>
              <div className="text-xs text-muted-foreground tracking-wide">
                Our Timeline
              </div>
            </div>
          </button>

          <div className="flex items-center gap-2">
            <Button
              variant={currentView === "timeline" ? "default" : "ghost"}
              size="sm"
              onClick={() => onViewChange("timeline")}
              className="gap-2 uppercase tracking-wider text-xs border border-primary/30 hover:border-primary/60"
            >
              <Film className="w-4 h-4" />
              <span className="hidden sm:inline">Reel</span>
            </Button>

            <Button
              variant={currentView === "messages" ? "default" : "ghost"}
              size="sm"
              onClick={() => onViewChange("messages")}
              className="gap-2 relative uppercase tracking-wider text-xs border border-primary/30 hover:border-primary/60"
            >
              <MessageCircle className="w-4 h-4" />
              <span className="hidden sm:inline">Notes</span>
              {unreadCount > 0 && (
                <span className="absolute -top-1 -right-1 w-5 h-5 bg-accent text-background rounded-full flex items-center justify-center text-xs border border-background">
                  {unreadCount}
                </span>
              )}
            </Button>

            <Button
              variant={currentView === "new-memory" ? "default" : "ghost"}
              size="sm"
              onClick={() => onViewChange("new-memory")}
              className="gap-2 uppercase tracking-wider text-xs border border-primary/30 hover:border-primary/60"
            >
              <Plus className="w-4 h-4" />
              <span className="hidden sm:inline">New Frame</span>
            </Button>

            <div className="ml-2 pl-2 border-l border-border">
              <Button
                variant="ghost"
                size="sm"
                onClick={onSignOut}
                className="gap-2 text-muted-foreground hover:text-foreground uppercase tracking-wider text-xs border border-transparent hover:border-primary/30"
              >
                <LogOut className="w-4 h-4" />
              </Button>
            </div>
          </div>
        </div>
      </div>
    </nav>
  );
}
